        <h1 class="title">Run Ads Sequentially</h1>
        <router-link class="nav-menu" :to="{ path: 'instream'}">Instream</router-link>
      </div>
    </div>
    <div class="row middle-xs" style="position:relative;z-index:1">
      <div class="col-xs-12 col-sm-6 col-md-6 col-lg-6 mb">
        <div class="box tabs-wrap">
          <Tabs :datas="param" v-model="tabActived"></Tabs>
          <div class="tabs-content">
            <div v-if="tabActived == 'tab1'">
              <div class="box-code">
