                <p>3. Ad viewability mode</p>
              </div>
              <div class="box-code">
                <h3>Guide</h3>
                <p>
                  <a target="_blank" href="https://docs.google.com/document/d/15eufwa37g9tDKhiOKGp9GP3omcSYN1YHzKaHrUZg7H4/edit?usp=sharing">
                    Hướng dẫn chi tiết
                  </a>
