                <p>
                  <a target="_blank" href="https://adstool.github.io/static/index-ads.html">
                    Site demo
                  </a>
                </p>
              </div>
              <div class="box-code">
                <h3>License</h3>
                <p>
                  Copyright (c) 2019-present, kimthiendung@gmail.com. Licensed under the terms of the
                  <a
                    href="//opensource.org/licenses/MIT"
                  >MIT License.</a>
