              </div>
            </div>
            <div v-if="tabActived == 'tab2'">
              <Form
                ref="form"
                :label-width="110"
                mode="block"
                :showErrorTip="showErrorTip"
                :rules="validationRules"
                :validOnChange="validOnChange"
                :model="model"
              >
                <!-- <FormItem label="Ad Active" single>
                  <div class="h-input-group">
                    <FormItem label="Key Active" :show-label="false" prop="adRunnerActive">
                      <input type="text" placeholder="Enter any key active" v-model="model.adRunnerActive" />
                    </FormItem>
                  </div>
                </FormItem> -->
                <FormItem label="Ad Key" single>
                  <div class="h-input-group">
                    <FormItem label="Ad Key" :show-label="false" prop="adRunnerKey">
                      <input type="text" placeholder="Enter any keyword" v-model="model.adRunnerKey" />
                    </FormItem>
                  </div>
                </FormItem>
                <FormItem label="Ad Viewability <seconds> (default: 0 = disable) " single>
                  <div class="h-input-group">
                    <FormItem label="Ad Key" :show-label="false" prop="adRunnerViewability">
                      <input
                        type="number"
                        placeholder="Enter the number of seconds viewability"
                        v-model="model.adRunnerViewability"
                      />
                    </FormItem>
                  </div>
                </FormItem>
                <FormItem label="Ad Cycle <seconds>" single>
                  <div class="h-input-group">
                    <FormItem label="Ad Cycle" :show-label="false" prop="adRunnerCycle">
                      <input
                        type="number"
                        placeholder="Enter the number of seconds cycle show ads"
                        v-model="model.adRunnerCycle"
                      />
                    </FormItem>
                  </div>
                </FormItem>
                <FormItem label="Ad Wait <seconds>" single>
                  <div class="h-input-group">
                    <FormItem label="Ad Wait" :show-label="false" prop="adRunnerWait">
                      <input
                        type="number"
                        placeholder="Enter the number of seconds waiting ads loading"
                        v-model="model.adRunnerWait"
                      />
                    </FormItem>
                  </div>
                </FormItem>
                <template v-for="(value, key) in model.adRunnerAds">
                  <FormItem label="Ads Code" single :key="key">
                    <div class="h-input-group">
                      <FormItem
