                        :show-label="false"
                        :prop="'adRunnerAds['+key+']'"
                        required
                      >
                        <input type="text" placeholder="URL" v-model="model.adRunnerAds[key]" />
                      </FormItem>
                      <span
                        class="action h-input-addon bg-red-color white-color"
                        v-tooltip
                        content="Remove Item"
                        @click="removeTodo(value)"
                      >
                        <i class="h-icon-minus"></i>
                      </span>
                    </div>
                  </FormItem>
                </template>
                <FormItem>
                  <Button
                    color="primary"
                    v-tooltip
                    content="Add Item"
                    icon="h-icon-plus"
                    @click="addTodo"
                  >Add Ads Code</Button>
                  <Button class="submit" color="blue" :loading="isLoading" @click="submit">Submit</Button>
                </FormItem>
              </Form>
            </div>
            <div v-if="tabActived == 'tab3'">
              <div class="box-code">
                <prism-editor :code="code" language="html" :readonly="true"></prism-editor>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-xs-12 col-sm-6 col-md-6 col-lg-6">
        <div class="box">
          <div id="adsBox" ref="adsBox"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "prismjs"
import PrismEditor from 'vue-prism-editor'
export default {
  components: {
    PrismEditor
  },
  data() {
    return {
      isReady: false,
      isLoading: false,
      tabActived: "tab2",
      code:'',
      param: {
        tab1: "Introduction",
        tab2: "Inspector",
        tab3: "Setup"
      },
      model: {
        adRunnerActive: window.btoa("MTU4MjAxNjAwNDQwMg3gnuD="),
        adRunnerKey: "",
        adRunnerViewability: 0,
        adRunnerCycle: 16,
        adRunnerWait: 8,
        adRunnerAds: [
          "<ins id=\"bs-ads-show\"></ins><script src=\"//static.blueseed.tv/ajs/ads-show.js\"><\/script>",
          "<img src onerror=\"var t=this;setTimeout(function(){t.src='https://www.gourmetads.com/wp-content/uploads/2019/05/fast-food-ads-mcdonalds-300x600.jpg'},2000)\"/>",
          "<ins id=\"bs-ads-show\"></ins><script src=\"//static.blueseed.tv/ajs/ads-show.js\"><\/script>"
        ]
      },
      validationRules: {
        required: [
          "adRunnerKey",
          "adRunnerViewability",
          "adRunnerCycle",
          "adRunnerWait",
          "adRunnerAds[].value"
        ]
      },
      showErrorTip: true,
      validOnChange: true
    };
  },
  methods: {
    // change(data) {
    //   var vm = this;
    //   vm.tabActived = 'tab3'
    //   this.$Message.info(`Switch to ${data.title}`, 1000);
    // },
    addTodo: function() {
      var vm = this;
      vm.model.adRunnerAds.push("");
    },

    removeTodo: function(todo) {
      if (this.model.adRunnerAds.length > 1) {
        this.model.adRunnerAds.splice(this.model.adRunnerAds.indexOf(todo), 1);
      }
    },

    submit() {
      this.isLoading = true;
      let validResult = this.$refs.form.valid();
      if (validResult.result) {
        this.$Message("Successful verification");
        setTimeout(() => {
          this.isLoading = false;
          this.runAds();
        }, 1000);
      } else {
        this.isLoading = false;
      }
    },

    genParseCode: function(adRunnerActive, adRunnerKey, adRunnerViewability, adRunnerCycle, adRunnerWait, adRunnerAds) {
      console.log(adRunnerActive)
      adRunnerActive = adRunnerActive ? window.atob(adRunnerActive) : "MTU4MjAxNjAwNDQwMg3gnuD=";
      console.log(adRunnerActive)
      var a =
        `\n<\script type="text/javascript" id="`+adRunnerKey+`">
        \nvar adRunnerActive = adRunnerActive || [];adRunnerActive.push("`+adRunnerActive+`");
        \nvar adRunnerKey = adRunnerKey || [];adRunnerKey.push("`+adRunnerKey+`");
        \nvar adRunnerViewability = adRunnerViewability || [];adRunnerViewability.push(`+adRunnerViewability+`);
        \nvar adRunnerCycle = adRunnerCycle || [];adRunnerCycle.push(`+adRunnerCycle+`);
        \nvar adRunnerWait = adRunnerWait || [];adRunnerWait.push(`+adRunnerWait+`);
        \nvar adRunnerAds = adRunnerAds || [];adRunnerAds.push(`+adRunnerAds+`);
        \nadRunnerInit();
        \n<\/script>`;
      return a;
    },

    pWriter: function(place, code) {
      var el = document.querySelector(place);
      el.innerHTML = '';
      code = unescape(code);
      if (window.postscribe) {
        console.log("write-1");
        window.postscribe(el, code, { autoFix: true, releaseAsync: false });
      } else {
        console.log("write-3");

        window.loadScripts(["https://cdnjs.cloudflare.com/ajax/libs/postscribe/2.0.8/postscribe.min.js"], function() {
          window.postscribe(el, code);
        });
      }
    },

    runAds: function() {
      var vm = this;
      if(!vm.isReady) vm.$Message("Ad Runner not ready!!!");

      var ads = JSON.stringify(vm.model.adRunnerAds).replace(/<\/script>/ig, "<\\\/script>");

      var adc = vm.genParseCode(vm.model.adRunnerActive, vm.model.adRunnerKey, vm.model.adRunnerViewability, vm.model.adRunnerCycle, vm.model.adRunnerWait, ads)

      vm.pWriter(
        "#adsBox",
        adc
      );

      var header = `
      \n<!-- Import Library Once -->
      \n<script src="//adstool.github.io/static/core/ads-series.js"><\/script>
      \n<!-- Import Code Setup -->
      `
      vm.code = header + adc;

      vm.tabActived = 'tab3'

    }
  },
  beforeCreate: function() {
    var vm = this;
    window.loadScripts(["static/core/ads-series.js"], function() {
      vm.$emit("loadScript");
    });
  },

  mounted: function() {
    var vm = this;

    vm.$on("loadScript", () => {
      vm.isReady = true
    });
  }
};
</script>
