<template>
  <div class="container">
    <div class="row middle-xs">
      <div class="col-xs-12 mb">
        <h1 class="title">Run Ads Series For Instream</h1>
        <router-link class="nav-menu" :to="{ path: '/'}">Outstream</router-link>
      </div>
    </div>
    <div class="row middle-xs" style="position:relative;z-index:1">
      <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6 mb">
        <div class="box tabs-wrap">
          <Tabs :datas="param" v-model="tabActived"></Tabs>
          <div class="tabs-content">

            <div v-if="tabActived == 'tab1'">
              <div class="box-code">
                <p>Ads show for all player video. It’s lightweight, flexible and fast.</p>
              </div>
              <div class="box-code">
                <h3>Lightweight</h3>
                <p>The modular structure provides excellent compression. Oh yes, it weights only ~7kb with every functionality included.</p>
              </div>
              <div class="box-code">
                <h3>Dependency</h3>
                <p>It's uses the library of IMA SDK (supports: vast, vpaid, vmap ... )</p>
              </div>
              <div class="box-code">
                <h3>License</h3>
                <p>Copyright (c) 2019-present, kimthiendung@gmail.com. Licensed under the terms of the <a href="//opensource.org/licenses/MIT"> MIT License.</a></p>
              </div>
            </div>

            <div v-if="tabActived == 'tab2'">
              <Form :label-width="110" mode="block">
                <!-- <FormItem label="Ad Active" single>
                  <div class="h-input-group">
                    <FormItem label="Key Active" :show-label="false" prop="adsIMAactive">
                      <input type="text" placeholder="Enter any key active" v-model="adsIMAactive" />
                    </FormItem>
                  </div>
                </FormItem> -->
                <template v-for="(value, key) in adTagUrl">
                  <FormItem label="Ad Tag URL" single :key="key">
                    <div class="h-input-group">
                      <FormItem label="Ad Tag URL" :show-label="false">
                        <input type="text" placeholder="URL" v-model="adTagUrl[key]" />
                      </FormItem>
                      <span class="action h-input-addon bg-red-color white-color" v-tooltip content="Remove Item" @click="removeTodo(value)"><i class="h-icon-minus"></i></span>
                    </div>
                  </FormItem>
                </template>
                <FormItem>
                  <Button color="primary" v-tooltip content="Add Item" icon="h-icon-plus" @click="addTodo">Add Url</Button>
                  <Button class="submit" color="blue" :loading="isLoading" @click="runAds">submit</Button>
                </FormItem>
              </Form>
            </div>

            <div v-if="tabActived == 'tab3'">
              <div class="box-code">
                <prism-editor :code="code" language="html" :readonly="true"></prism-editor>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
        <div class="box">
          <div id="playerWrap">
            <div id="player" data-plyr-provider="vimeo" data-plyr-embed-id="76979871"></div>
          </div>
          <div id="console">
            <div id="console-name">event</div>
            <div id="event" ref="event">
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "prismjs"
import PrismEditor from 'vue-prism-editor'

export default {
  components: {
    PrismEditor
  },
  data() {
    return {
      isLoading: false,
      tabActived: "tab2",
      adsIMAactive: window.btoa("MTYwNDkxNjQ0OTkxMw3gnuD="),
      code: '',
      param: {
        tab1: "Introduction",
        tab2: "Video Inspector",
        tab3: "Setup"
      },
      adTagUrl: [
        'https://pubads.g.doubleclick.net/gampad/ads?sz=640x480&iu=/124319096/external/single_ad_samples&ciu_szs=300x250&impl=s&gdfp_req=1&env=vp&output=vast&unviewed_position_start=1&cust_params=deployment%3Ddevsite%26sample_ct%3Dlinearvpaid2js&correlator=',
        'https://static.blueseed.tv/ajs/ad-core/xml/v4_inread.xml'
      ],
    };
  },
  methods: {
    addTodo: function () {
      var vm = this;
      vm.adTagUrl.push('')
    },

    removeTodo: function (todo) {
      this.adTagUrl.splice(this.adTagUrl.indexOf(todo), 1)
    },

    submit() {
      this.isLoading = true;
      let validResult = this.$refs.form.valid();
      if (validResult.result) {
        this.$Message('Successful verification');
        setTimeout(() => {
          this.isLoading = false;
        }, 1000);
      } else {
        this.isLoading = false;
      }
    },
    genCode: function (str, adsIMAactive) {
      var a = `
      \n<!-- Import Library Once -->
      \n<script src="//adstool.github.io/static/core/ads-ima.js"><\/script>
      \n<!-- Import Code Setup -->
      \n<script>
        \nvar adsDisplay = adsIMA.init("#playerWrap", "`+adsIMAactive+`");
        \n`+str+`adsDisplay.runAds();
      \n<\/script>
      `
      return a;
    },
    runAds: function () {
      var vm = this;
      var adsIMAactive = window.atob(vm.adsIMAactive)

      var adsDisplay = adsIMA.init("#playerWrap", adsIMAactive);
      var temp = '';
      for(var i in vm.adTagUrl){
        adsDisplay.setAdsRequest(vm.adTagUrl[i]);
        temp += 'adsDisplay.setAdsRequest('+vm.adTagUrl[i]+');\n'
      }

      vm.code = vm.genCode(temp, adsIMAactive);
      vm.tabActived = 'tab3';

      adsDisplay.runAds();

      var _events = ['skip','mute','volumeChange','click','loaded','start','firstQuartile','midpoint','thirdQuartile','complete']
      var _onAdEvent = function (adEvent) {
        var ebox = vm.$refs['event'];
        var item = document.createElement('p');
        if(adEvent.type === 'loaded'){
          item.textContent = '--'+adEvent.type+': ' + adEvent.detail;
        }
        else if (adEvent.type === 'complete'){
          item.textContent = '------------complete-------------';
        }
        else{
          item.textContent = '--'+adEvent.type;
        }
        ebox.appendChild(item);
        item.scrollIntoView();
        // console.log(ebox.scrollTop, ebox.scrollHeight)
        
        // ebox.scrollTop = ebox.scrollHeight;
        // console.log(adEvent.type, adEvent.detail)
      }
      for (var index in _events) {
        adsDisplay.adContent.addEventListener(_events[index], _onAdEvent);
      }
    },
  },
  beforeCreate: function() {
    var vm = this;
    window.loadScripts([
        "https://cdn.plyr.io/3.5.6/plyr.polyfilled.js",
        "static/core/vanilla-tilt.min.js",
        "//imasdk.googleapis.com/js/sdkloader/ima3.js",
        "static/core/ads-ima.js"
      ], function () {
        vm.$emit('loadScript')
    });
    
  },
  
  mounted: function() {

    var vm = this;

    vm.$on('loadScript', () => { 
      var vm = this;
      const player = new Plyr("#player");

      VanillaTilt.init(document.querySelectorAll(".tilt"));
    });
  }
};
</script>
